.section-hero {
    background-image: url("../../../assets/images/static/home/bg-1.jpg");
    background-size: cover;
    background-position: top center;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 100px;
    max-height: 650px;
}

.section-hero .hero-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.section-hero .hero-content .main-heading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: var(--color-white);
    font-weight: 700;
    font-size: 60px;
    white-space: nowrap;
}

.section-hero .hero-content .btn-learn-more {
    background-color: var(--color-dark);
    border-color: var(--color-dark);
    font-size: 24px;
    font-weight: 400;
    letter-spacing: 1px;
    margin: 50px 0 20px 0;
}

.section-hero .hero-content .question-header {
    font-size: 24px;
    margin-bottom: 70px;
}

.section-hero .hero-content .input-group .btn-search {
    background-color: transparent;
    border-color: transparent;
    color: var(--color-dark);
    font-size: 24px;
    border-radius: 0;
    border-bottom: 2px solid var(--color-white);
}

.section-hero .hero-content .input-group .form-control {
    font-size: 18px;
    background-color: transparent;
    border: none;
    border-bottom: 2px solid var(--color-white);
    border-radius: 0;
}

.section-hero .hero-content .input-group .form-control::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: var(--color-dark);
    opacity: 70%; /* Firefox */
}

.section-hero .hero-content .input-group .form-control:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: var(--color-dark);
    opacity: 70%;
}

.section-hero .hero-content .input-group .form-control::-ms-input-placeholder { /* Microsoft Edge */
    color: var(--color-dark);
    opacity: 70%;
}

.section-hero .hero-content .input-group .form-control:focus {
    box-shadow: none;
    outline: none;
}


.section-hero .plane-img-container img {
    height: 100%;
    width: 100%;
    object-fit: contain;
}

.section-content {
    border: 15px solid var(--color-white);
    border-left: none;
    border-right: none;
}

.section-content + .section-content {
    border-top: none;
}

.section-black {
    background-color: var(--bg-home-black);
    color: var(--color-white);
}

.section-dark {
    background-color: var(--bg-home-dark);
    color: var(--color-white);
}

.section-light {
    background-color: var(--color-white);
    color: var(--color-dark);
}

.section-content .img-holder {
    position: relative;
    height: 100%;
}

.section-content .img-holder.left {
    margin-left: calc(var(--bs-gutter-x) * -.5);
}

.section-light .img-holder.right {
    margin-right: calc(var(--bs-gutter-x) * -.5);
}

.section-content .img-holder img {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.section-content .content-container {
    padding: 50px;
}

.sub-heading {
    font-size: 44px;
    text-transform: uppercase;
    font-weight: 700;
}

p + p {
    margin-top: 30px;
}

p {
    margin: 0;
}

.section-home-news {
    margin-bottom: 15px;
}

.section-home-news .card-news {
    border-color: var(--bg-home-black);
    color: var(--bg-home-black);
    border-radius: 0;
}

.section-home-news .card-news img {
    height: 550px;
    width: 100%;
    object-fit: cover;
}

.section-home-news .card-news .card-body {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
    padding-top: 30px;
    padding-bottom: 30px;
}

.section-home-news .card-news .card-body .category {
    font-size: 18px;
    font-weight: 700;
    margin: 0;
}

.section-home-news .card-news .card-body .post-title {
    font-size: 32px;
    font-weight: 700;
    margin: 0;
}

.section-home-news .card-news .card-body .btn-dark {
    background-color: var(--bg-home-black);
    border-color: var(--bg-home-black);
}
